@use "../../App.scss";

.container {
  margin-top: 4rem;
}

.container > p {
  font-size: 2rem;
  margin-bottom: 3rem;
}

.grid {
  display: grid;
  grid-template-columns: 33% 33% 33%;
  justify-content: start;
  gap: 1.5rem;

  @media only screen and (max-width: 600px) {
    grid-template-columns: 20% 20% 20%;
  }

  .column {
    @media only screen and (max-width: 600px) {
      grid-column: 1/-1;
      gap: 0rem;
    }

    > div {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      margin-bottom: 1rem;
      border-left: #d6d6d6 1px solid;

      @media only screen and (max-width: 600px) {
        margin-bottom: 0.5rem;
      }
    }
  }
}

.icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  color: App.$secondary;
  padding: 1rem;
  border-radius: 100%;
  height: 50px;
  width: 50px;
}
