@use "../../App.scss";

.container {
  margin-top: 2rem;
  margin-bottom: 6rem;
}

.grid_container {
  display: grid;
  grid-template-columns: 50% 50%;
  background-color: #f5f5f5;
  border-top-right-radius: 0.6rem;
  border-bottom-right-radius: 0.6rem;
  border-bottom-left-radius: 0.6rem;

  >div{
    @media only screen and (max-width: 600px){
      grid-column: 1/-1;
    }
  }

  // &:hover .spec{
  // clip-path: circle(75% );
  // }
}

.carousel{
  background-color: #d6d6d6;
  border-bottom-left-radius: .6rem;

  @media only screen and (max-width: 600px){
    border-bottom-left-radius: 0rem;
  }

}

.spec {
  padding-right: 2rem;
  position: relative;
  padding-top: 1rem;

  &:hover > p span {
    border-bottom: App.$primary 2px solid;
  }

  > ul {
    list-style: none;
    padding-left: 1.5rem;

    li {
      padding-bottom: 0.2rem;

      &:before {
        content: "+";
        font-size: 0.7rem;
        display: inline-block;
        font-weight: bold;
        margin-right: 0.5rem;
        color: App.$primary;
      }
    }
  }

  > p {
    margin-bottom: 0.8rem;
    padding-left: 2.2rem;

    span {
      font-size: 2.3rem;
      border-bottom: #f0f0f0 2px solid;
      transition: all 0.2s ease-in-out;
    }
  }
  // background-color: yellow;
  // clip-path: circle(10% at 0% 0%);
  // transition: clip-path .5s ease-in-out
}

.arrowPrev {
  position: absolute;
  top: 50%;
  left: 30px;
  z-index: 1;
  transform: translate(-50%, -50%);
  width: 30px;
  background-color: black;
  color: white;
  padding: 1.5rem 0.5rem;
  cursor: pointer;
  margin: 0;
  opacity: 25%;
  border: 0;
  border-radius: 0.5rem;
}

.arrowNext {
  position: absolute;
  top: 50%;
  right: 0px;
  z-index: 1;
  transform: translate(-50%, -50%);
  width: 30px;
  background-color: black;
  color: white;
  padding: 1.5rem 0.5rem;
  cursor: pointer;
  margin: 0;
  opacity: 25%;
  border: 0;
  border-radius: 0.5rem;
}

.arrowPrev,
.arrowNext {
  &:hover {
    opacity: 65%;
  }
}

.button_container {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 2;
  width: 95%;
  display: flex;
  justify-content: flex-end;
  margin-right: 2rem;

  @media only screen and (max-width: 600px){
    position: relative;
  }

  button {
    padding: 0.6rem;
    text-decoration: none;
    border-radius: 1rem;
    border: 0;
    background: App.$primary;
    color: black;
    font-weight: bold;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
    margin-top: 2rem;
    margin-bottom: 1.8rem;
    width: 50%;
    cursor: pointer;
    font-size: 0.8rem;

    transition: all 0.2s ease-in-out;

    &:hover {
      background-color: #ffe76e;
      color: #363636;
    }
  }
}
