nav {
  padding: 0 2rem;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  position: relative;
}

.logo {
  margin-right: auto;
  font-weight: bold;
  font-size: 1.5rem;
}

.container1 {
  @media only screen and (min-width: 600px) {
    margin-left: auto;
    margin-right: auto;
    max-width: 1200px;
  }
}

.container2 {
  padding: 1.5rem 0;
  display: flex;
  justify-content: end;
  align-items: center;
}

.sections_buttons {
  display: flex;
  align-items: center;

  > div {
    @media only screen and (max-width: 600px) {
      display: none;

      &:last-child {
        display: block;
      }
    }
  }
}

.sections_buttons {
  div {
    margin-left: 1.5rem;
    cursor: pointer;

    &:hover {
      color: #b0b0b0;
    }
  }
}
